import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class CartService {
  items = [];

  constructor(
      private http: HttpClient
  ) {
  }

  addToCart(product) {
    return this.items.push(product);
  }

  getItems() {
    return this.items;
  }

  clearCart() {
    this.items = [];
    return this.items;
  }

  get shippingPrices(){
    return this.http.get('/assets/shipping.json')
  }
}
